import React from 'react';
import { useNavigate } from 'react-router-dom';
import Mattress from '../assets/mattress.png'

const MattressForm = ({props}) => {
    const route = useNavigate()
    return (
        <div 
            className='mattress_form_container'
            onClick={() => route(`/home/${props.id}`)}
        >
            <div className='mattress_form'>
                <div className='mattress_img_container'>
                    <img className='mattress_img' src={Mattress}/>
                </div>
                <div>
                    <div className='mattress_options'> {props.id}</div>
                    <div className='mattress_options'>: {props.producer}</div>
                    <div className='mattress_options'>: {props.type}</div>
                    <div className='mattress_options'>: {props.size} </div>
                    <div className='mattress_options'>: {props.heigh} </div>
                    <div className='mattress_options'>  1 . : {props.load} </div>
                    <div className='mattress_options'>: {props.rigidity}</div>
                    <div className='mattress_options bodyoption'>{props.body}</div>
                </div>
            </div>
            <div className='price'>
                {props.price} 
            </div>
        </div>
    );
};

export default MattressForm;