import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import ChekoutModal from '../components/UI/ChekoutModal';
import FormInput from '../components/UI/FormInput';
import FormDeliveryInput from '../components/UI/FormDeliveryInput';
import FormPaymentInput from '../components/UI/FormPaymentInput';
import FormLegend from '../components/UI/FormLegend';
import FormAside from '../components/UI/FormAside';

const Checkout = () => {
    const {purchasedItemsNumber} = useSelector(state => state.cart)
    const route = useNavigate()
    useEffect(() => {
        if (!purchasedItemsNumber) { 
            route('/home')
        }
    }, [])
    return (
        <div>
            <ChekoutModal/>
            <form>
                <div className='checkout_title'>   </div>
                <div className='checkout_container'>
                    <div className='checkout_your_data_container'>
                        <fieldset>
                            <FormLegend item={1}>    </FormLegend>
                            <div className='checkout_your_data'>
                                <FormInput
                                    containerStyle={'data_container_surname'}
                                    inputStyle={'checkout_input_surname'}
                                    name={'buyerSurname'}
                                    placeholder={''}
                                />
                                <FormInput
                                    containerStyle={'data_container_name'}
                                    inputStyle={'checkout_input_name'}
                                    name={"buyerName"}
                                    placeholder={''}
                                />
                            </div>
                                <FormInput
                                    containerStyle={'data_container_phone'}
                                    inputStyle={'checkout_input_phone_number'}
                                    name={'buyerPhone'}
                                    placeholder={''}
                                />
                        </fieldset>
                        <fieldset>
                            <FormLegend item={2}>  </FormLegend>
                            <FormDeliveryInput name={"pickup"}>   / </FormDeliveryInput>
                            <FormDeliveryInput name={"targeted"}>   </FormDeliveryInput>
                        </fieldset>
                        <fieldset>
                            <FormLegend item={3}>   </FormLegend>
                            <FormPaymentInput name={"byCard"}>   </FormPaymentInput>
                            <FormPaymentInput name={"byCash"}>   </FormPaymentInput>
                        </fieldset>
                        <fieldset>
                            <FormLegend item={4}>   </FormLegend>
                            <div className='checkout_your_data'>
                                <FormInput
                                    containerStyle={'data_container_surname'}
                                    inputStyle={'checkout_input_surname'}
                                    name={'receiverSurname'}
                                    placeholder={''}
                                />
                                <FormInput
                                    containerStyle={'data_container_name'}
                                    inputStyle={'checkout_input_name'}
                                    name={'receiverName'}
                                    placeholder={''}
                                />
                            </div>
                            <div className='checkout_your_data'>
                                <FormInput
                                    containerStyle={'data_container_email'}
                                    inputStyle={'checkout_input_surname'}
                                    name={'receiverEmail'}
                                    placeholder={'@Email'}
                                />
                                <FormInput
                                    containerStyle={'data_container_phone'}
                                    inputStyle={'checkout_input_phone_number'}
                                    name={'receiverPhone'}
                                    placeholder={''}
                                />
                            </div>
                        </fieldset>
                    </div>
                    <FormAside />
                </div>
            </form>
        </div>
    );
};

export default Checkout;